.run{
    display: flex;
    width: 50%;    
}
.run img{
    width: 100%;
}
.runText{
    display: flex;
    color: white;
    width: 50%;
    flex-direction: column;
    padding: 3rem;
    background-color: rgb(237, 42, 75);
}
.runText h1{
    font-size: 50px;
    font-weight: lighter;
}
.runText button{
    padding: 1rem 1rem;
    letter-spacing: 2px;
    font-weight: bold;
}