.finalText{
    display: inline-flex;
    justify-content: space-between;
    width: 100%;
    padding: 3rem;
}

.final1{
    display: flex;
    width: 25%;
    flex-direction: column;
    align-items: center;
    margin: auto;
    
}
.final1 h1{
    letter-spacing: 2px;
    font-size: 40px;
    font-weight: lighter;
}
.final1 p{
    font-size: 25px;
    margin: 0;
}
.emptyfinal{
    width: 100%;
    height: 8px;
    background-color: brown;
    margin-top: 2rem;
}
.final2{
    display: flex;
    width: 30%;
    flex-direction: column;
    margin: auto;
}
.final2 p{
    margin: 2rem 0rem;
    font-size: 20px;
}
.final1 img{
    width: 100%;
}